@import url(https://fonts.googleapis.com/css2?family=Bellota+Text:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bellota+Text:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body {
  background-color: #000000;
}
.App {
  font-family: sans-serif;
  background-color: #000000;
  color: #eee;
  min-height: 100vh;
  max-width: 100vw;
}

.darkDiv {
  background-color: #2d2d2d;
  color: #eee;
}
.pages {
  padding: 3%;
  padding-top: 4%;
  margin-top: 20px;
}

::-webkit-scrollbar {
  width: 5px;
}
::-webkit-scrollbar-track {
  background-color: black;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(253, 253, 253);
}

a {
  color: #000;
  text-decoration: none;
}

header {
  background-color: #fafafa;
  height: 60px;
}
header .nav-item {
  padding: 0 15px;
  border-right: 2px solid #eee;
  height: 100%;
  display: inline-flex;
  align-items: center;
}
header .nav-item.active {
  background: #000;
  color: #fff;
}

/* Events page style */
.events-corousal {
  width: 100%;
  height: 100%;
}

.featured-events {
  min-height: 500px;
}

.events-description-container {
  text-align: center;
}

.events-description {
  margin: auto auto;
  text-align: center;
  margin-top: 30%;
}
.center-items {
  align-items: center;
  margin: auto auto;
}

.contact {
  width: 500px;
  margin: auto auto;
  text-align: left;
  margin-top: 5%;
  padding: 20px;
}

.contact-icon {
  margin-top: 5px;
  margin-left: 10%;
  border-radius: 15%;
  padding: 5px;
  width: 230px;
}

.event-header {
  margin-top: 30px;
}
.teams-page {
  margin-top: 30px;
}

.event-page {
  margin-top: 30px;
}
.container.contactContainer {
  max-width: 100vw;
  text-align: center;
}
.container.contactContainer .contact-icon {
  padding: 0;
  margin: 0;
}

@media (max-width: 600px) {
  .contactContainer .row {
    text-align: center;
  }
  .contactContainer .contact {
    max-width: 80vw;
    padding: 0;
    margin: 0;
  }
  .contactContainer #contactImage {
    padding: 50px 10px;
    max-width: 90vw;
  }

  .contactContainer .contact-icon {
    display: none;
  }
}

.eventCard {
    background-color: rgb(0, 0, 0);
    color: blanchedalmond; 
    border-color: black;
    border-radius: 15px;

}
.event-image{
    width: 100%;
    height: auto;
    margin: auto auto;
    vertical-align: center;
    border-radius: 10%;
}
.poster-container{
    padding: 3%;
}
.watch-model{
    background-color:#2d2d2d;
}

.event-body{
    color: rgb(255, 255, 255);
}

.event-desc{
    color: #b0b0b0
}

.iframe-player{
    min-width: 90vw;
    min-height: 60vh;
    max-height: 70vh;
}
