*{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   font-family: 'Bellota Text', cursive;
}

.container
{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
}
.container .head1{
   width: 100%;
   height: 100%;
   text-align: center;
   margin: 10px;
}
.container .card
{
   
   width: 300px;
   height: 385px;
   padding: 60px 30px;
   margin: 20px;
   background: #2d2d2d;
   box-shadow: 0.1em 0.1em 0.5em #ffffff,
               -0.1em -0.1em 0.4em #ffffff;
   border-radius: 10px;
}

.container .card .content
{
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}
.container .card .content .imgBx
{
   width: 150px;
   height: 150px;
   border-radius: 50%;
   position: relative;
   margin-bottom: 20px;
   overflow: hidden;
}
.container .card .content .imgBx img
{
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   object-fit: cover;
}
.container .card .content .contentBx h5
{
   color: #ffffff;
   font-size: 1.2rem;
   font-weight: bold;
   text-align: center;
   letter-spacing: 1px;
}
.container .card .content .contentBx h6
{
   color: #c9c9ca;
   font-size: 1rem;
   font-weight: bold;
   text-align: center;
}
.container .card .content .sci
{
   margin-top: 20px;
}
.container .card .content .sci a
{
   text-decoration: none;
   color: #6c758f;
   font-size: 30px;
   margin: 10px;
   transition: color 0.4s;
}
.container .card .content .sci a:hover
{
   color: #0196e3;
}
